<template>
  <div class="container">
    <div class="pic-top-bg" :style="{'height': scrollHeight + 'rem','background': `url(${storeSign.storeBgImg}) 0% 0% / 100% 5.96rem no-repeat`}">
      <div class="top-bg-masking"/>
      <div class="top-gradient"/>
    </div>
    <!-- 标题栏 -->
    <div class="shop-nav-bar">
      <div class="pic-top-bg" :style="{'background': `url(${storeSign.storeBgImg}) 0% 0% / 100% 5.96rem no-repeat`, height: '1.1rem'}">
        <div class="top-bg-masking" style="height: 1.1rem"/>
      </div>
      <div class="top-back-btn" @click="goBack" v-if="!$route.query.browerFirstIn"/>
      <div class="search-bar" @click="openSearch">
        <div/>
        <img :src="`${$cdnImageUrl}/shop/icon_shop_search.png`"/>
        <span>搜索</span>
      </div>
      <div class="share-btn" v-if="showShare" @click="openShareModal"/>
    </div>
    <skeleton :zIndex="3" :nums="4" :iheight="'.4rem'" v-if="isSkeletonLoading"/>
    <div v-else>
      <div class="content"  id="shop-container">
        <shop-header
          v-if="shopBasicInfo"
          :taskId="taskId"
          :stageId="stageId"
          :shopInfo="shopBasicInfo"
          :storeSign="storeSign"
          @toogleModal="toogleModal"
          @updateShopInfo="getMemberStoreId"
          :hasSubStore="subStoreList.length > 0"
        />
        <address-no-data v-if="isOutOfLoaction" :location="location" @changeAddr="changeLocation"/>
        <keep-alive v-else>
          <tab-recommend
            v-if="bottomTabType === 'tab-home'"
            :storeId="shopStoreId"
            :shareId="shareId"
            :storeSign="storeSign"
            :storeSceneData="storeSceneData"
            @scroll="scrollInfo"
          />
          <tab-product
            v-if="bottomTabType === 'tab-product'"
            :storeId="shopStoreId"
            :shareId="shareId"
            :isBottomBar="true"
          />
          <tab-category
            v-if="bottomTabType === 'tab-category'"
            :storeId="shopStoreId"
            :shareId="shareId"
            :storeSign="storeSign"
          />
        </keep-alive>
      </div>
    </div>
    <bottom-tab
      v-if="!isSkeletonLoading && !isOutOfLoaction"
      :storeId="shopStoreId"
      :showSceneTab="storeSign.synStoreType!=='9'"
      :storeSceneData="storeSceneData"
      :storeCode="storeSign.storeCode"
      :currentTab="bottomTabType"
      @changeTab="changeBottomTab"/>
    <address-list-modal :storeId="shopStoreId" :subStoreList="subDisplayStoreList" :showAddress.sync="showAddress"/>
    <van-popup
      overlay-class="zindex9998"
      v-model="addrShow"
      :style="{ width: '6.22rem', height: '100vh' }"
      position="right">
        <!-- 地址选择列表 -->
        <addr-select
          :addrList="addrData"
          v-if="addrShow"
          @onHide="onHideAddr"
          @handleLocation="handleLocation"
          @otherLocation="otherLocationOption"
        />
    </van-popup>
    <van-popup
      overlay-class="zindex9998"
      v-model="locationshow"
      :style="{ width: '6.22rem', height: '100vh' }"
      position="right">
        <!-- 选择地理位置 -->
        <change-geographic-location
          v-if="locationshow"
          :storeId="shopStoreId"
          :isShowBack="true"
          @onHide="onHideLocation"
          @closeAndBack="onHideLocation"
          @handleLocation="handleLocation" />
    </van-popup>
    <!-- <div class="go-top" v-show="showTotop" @click="toGotop">
      <img :src="`${$cdnImageUrl}/home/new/gotop.png`" />
    </div> -->
    <!-- 返回顶部 -->
     <top-feedBack
     :isShow="showTotop"
     :fromTitle="'店铺详情'"
     :classList="{'has-bottom-bar': true}"
     @hiddenFeedBack="hiddenFeedBack"
     @toTop="toGotop"></top-feedBack>
    <custom-share :show.sync="showShareModal" :copyCustomLink="true" @copyLink="copyLink" @customShare="doShare" @pictureShare="openPictureShare"></custom-share>
    <picture-share :show.sync="showPictureShare"></picture-share>
  </div>
</template>
<script>
import ClipboardJS from 'clipboard';
import { mapGetters } from 'vuex'
import URL from '@/http/url'
import skeleton from '@/components/skeleton';
import topFeedBack from '@/components/top-feedBack/index'
import shopHeader from './component/shopHeader';
import bottomTab from './component/bottom-tab/bottomTab';
import tabRecommend from './component/bottom-tab/tabRecommend';
import {
  manage,
  getStoreSign,
  checkPrivateDomain,
  getSubStoreList,
  getStoreSceneNew,
} from '@/http/service/shop';
import personalApi from '@/http/service/index';
const addressListModal = () => import('./component/addressListModal');
const customShare = () => import('@/components/custom-share/custom-share')
const pictureShare = () => import('@/components/picture-share/picture-share')
const tabCategory = () => import('./component/bottom-tab/tabCategory');
const tabProduct = () => import('./component/bottom-tab/tabProduct');
const addressNoData = () => import('./component/address-no-data');
const addrSelect = () => import('./component/addressList/addr-select');
const changeGeographicLocation = () => import('./component/addressList/change-geographic-location');
/** 千店千面 店铺首页
 * 店铺分类
 * 【智家店】
 * 【乡镇店】
 * 【001号店】底部tab无商品分类改为三翼鸟预约报名
 * 【生态店】不显示地址
 * 【品类店】不显示地址
 * 【品牌店】不显示地址
*/

let ua = 'H5' // ua决定跳转链接与跳转方式
// if (utils.isSyBird()) {
//   ua = 'Flutter'
// } else if (utils.isSyBirdMP()) {
//   ua = 'WeChat'
// }

const jumpType = {
  'H5': {
    val: 5,
    urlKey: 'detailUrl'
  },
  'WeChat': {
    val: 22,
    urlKey: 'weChatDetailUrl'
  },
  'Flutter': {
    val: 16,
    urlKey: 'flutterDetailUrl'
  },
}[ua]

export default {
  components: {
    skeleton,
    shopHeader,
    bottomTab,
    tabRecommend,
    tabCategory,
    tabProduct,
    addressListModal,
    addressNoData,
    changeGeographicLocation,
    addrSelect,
    customShare,
    pictureShare,
    topFeedBack
  },
  data() {
    return {
      scrollHeight: 5.96,
      showShare: this.$util.isSgRn() || this.$util.isHaierApp() || this.$util.isIhaierApp(),
      isSkeletonLoading: true,
      bottomTabType: 'tab-home',
      shopStoreId: '', // 所属店铺Id
      shareId: '',
      shopBasicInfo: {},
      storeSign: {
        showLogo: 1,
        logoUrl: `${this.$cdnImageUrl}/uplus_logo.png`, // 店铺图标
        storeType: 1, //  0-非旗舰店 1-旗舰店 2-售后店 3-生态店 4-品类店 5-品旗店 6-001号店
        shopStoreCategory: '', // 店铺二级分类页面 01-乡镇店 11-智家店 12-三翼鸟店 13-综合店 31-生态店 32-家装 33-智能互联 34-安防 35-生活家电
        storeBgImg: '', // 头部背景
        storeCode: '', // 店铺88码
        storeAddress: '', // 店铺地址
        latitude: '',
        longitude: '',
        storeName: '',
        o2oStoreName: '',
        chosenTab: 1, // 精选是否显示
      },
      storeSceneData: {}, // 场景数据
      showAddress: false, // 显示分店地址
      showTotop: false,
      addrShow: false, // 显示登录人地址列表
      locationshow: false, // 显示选择地址弹窗
      addrData: [], // 登录人地址列表
      isOutOfLoaction: false, // 超出私域范围
      subStoreList: [], // 店铺地址列表
      showShareModal: false, // 显示分享面板
      showPictureShare: false, // 显示画报分享
      taskId: '',
      stageId: '',
      freeze: Object.freeze({
        localData: {
          scrollTop: 0,
          isFirst: true
        }
      }),
    };
  },
  computed: {
    ...mapGetters(['mId']),
    ...mapGetters('mymanage', ['getIdentify']),
    location() {
      return this.$store.getters['getlocationInfo']
    },
    subDisplayStoreList () {
      if (this.storeSign.mdCode && this.storeSign.shareUserIdentify === 1 && this.getIdentify === 0) {
        return this.subStoreList.filter(v => v.mdCode === this.storeSign.mdCode)
      } else {
        return this.subStoreList
      }
    }
  },
  activated() {
    this.$util.gioTrackWithVariable('MB15615', {
      storeId: this.$route.query.storeId ? this.$route.query.storeId + '' : 'null'
    })
    this.$nextTick(() => {
      setTimeout(() => {
        document.getElementById('app-warp') && document.getElementById('app-warp').scrollTo(0, this.freeze.localData.scrollTop || 0);
      }, 100);
    });
    if (!this.freeze.localData.isFirst) {
      this.getMemberStoreId();
    } else {
      this.freeze.localData.isFirst = false;
    }
  },
  created() {
    this.$store.dispatch('mymanage/fetchManageInfo')
    this.shareId = this.$route.query.shareId || ''
    this.taskId = this.$route.query.taskId || ''
    this.stageId = this.$route.query.stageId || ''
    this.getMemberStoreId()
    if (this.$store.getters.getLoginStatus) {
      this.getAddress()
    }
    if (this.$util.isSgRn()) {
      setTimeout(() => {
        window.changeH5Location = (res) => {
          console.log('掌柜定位信息', res);
          if (res.longitude) {
            this.$store.dispatch('doLocationInfo', { latitude: res.latitude, longitude: res.longitude, force: true });
          }
        }
      }, 0);
    }
    this.fetchRecordShareRelation()
  },
  destroyed() {
    document.getElementById('app-warp').removeEventListener('scroll', this.bindScroll);
  },
  beforeRouteLeave (to, from, next) {
    if (document.getElementById('app-warp')) {
      this.freeze.localData.scrollTop = document.getElementById('app-warp').scrollTop;
    }
    localStorage.removeItem('baseStoreId')
    localStorage.removeItem('mdCode')
    next();
  },
  methods: {
    scrollInfo(info) {
      if (info.isFixed) {
        this.scrollHeight = 1.66
      } else {
        this.scrollHeight = 5.96
      }
      console.log('===>scroll', info)
    },
    getSceneList() {
      const param = {
        current: 1,
        size: 4,
        storeId: this.storeSign.storeId,
      };
      getStoreSceneNew(param).then(res => {
        this.loading = false;
        if (res.success) {
          let sceneUrl = ''

          if (res.data[`senceList${ua}Url`]) {
            sceneUrl = res.data[`senceList${ua}Url`]
            if (!sceneUrl.includes('?')) {
              sceneUrl += '?'
            } else {
              sceneUrl += '&'
            }
            sceneUrl += `userId=${this.$store.state.ucId ? this.$store.state.ucId : ''}&userAccessToken=${this.$store.getters.getUserCenterToken}`
          }

          this.storeSceneData = {
            ...res.data,
            storeSenceUrl: this.$util.injectISOAddressCodeToURL(sceneUrl, this.$store.getters.getlocationInfo),
            jumpType: jumpType.val,
            sceneUrlKey: jumpType.urlKey
          }
          console.log('场景数据', this.storeSceneData)
        } else {
          this.$toast(res.message || '获取数据失败，请稍后重试');
        }
      });
    },

    // 复购分佣接口调用
    fetchRecordShareRelation() {
      if (this.$store.state.shareId) {
        this.$http.get(URL.recordShareRelation, {
          params: {
            sharerMemberId: this.$store.state.shareId || '',
            storeProductId: '',
            storeId: this.$route.query.storeId || ''
          }
        })
      }
    },

    /** 获取关联的storeId */
    getMemberStoreId() {
      manage({
        storeId: this.$route.query.storeId,
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        ionId: this.location.districtId,
        street: this.location.townshipId,
        shareStoreId: this.shareId, // 分享人id
      }).then(res => {
        if (res.success) {
          this.shopStoreId = res.data.storeId ? (res.data.storeId + '') : '';
          this.shopBasicInfo = res.data;
          localStorage.setItem('baseStoreId', this.shopStoreId)
          this.getStoreSign();
          this.checkPrivateDomain();
          this.getSubStore()
        } else {
          this.$toast(res.message)
        }
        // // 二次分享
        // this.$store.dispatch('WeChatSecondShare', {
        //   title: this.shopBasicInfo.storeName,
        //   desc: '海尔智家商城，海尔家电正品保障',
        //   thumImage: '',
        // })
      })
    },
    hiddenFeedBack() {
      this.showTotop = false
    },
    getStoreSign() {
      let param = {
        shareId: sessionStorage.getItem('shareUserId') || '',
        storeId: this.shopBasicInfo.storeId,
        mdCode: sessionStorage.getItem('shareMdCode') || ''
      }
      if (this.location) {
        param.longitude = this.location.longitude
        param.latitude = this.location.latitude
      }
      getStoreSign(param).then(res => {
        this.isSkeletonLoading = false;
        if (res.success) {
          this.storeSign = {
            ...this.storeSign,
            ...res.data,
          };
          if (!res.data.logoUrl) {
            if (Number(res.data.synStoreType) !== 9 || this.$util.isSyBird()) {
              this.storeSign.logoUrl = `${this.$cdnImageUrl}/sybird_default_logo.png`;
            } else {
              this.storeSign.logoUrl = `${this.$cdnImageUrl}/uplus_logo.png`;
            }
          }
          if (res.data.synStoreType !== '9') this.getSceneList() // 店铺升级为三翼鸟店铺时才会请求场景数据
          localStorage.setItem('mdCode', res.data.mdCode)
          this.$nextTick(() => {
            document.getElementById('app-warp').addEventListener('scroll', this.bindScroll);
          })
        } else {
          console.log('获取店招模块失败');
          this.$toast(res.message || '获取店招模块失败')
        }
      });
    },
    checkPrivateDomain() {
      checkPrivateDomain({
        storeId: this.shopStoreId,
        street: this.location.townshipId,
      }).then(res => {
        if (res.success) {
          this.isOutOfLoaction = !res.data;
        }
      });
    },
    goBack() {
      this.$store.dispatch('doHistoryBack');
    },
    changeBottomTab(index) {
      console.log('changeBottomTab', index)
      this.scrollHeight = 5.96
      // 25093 埋点
      switch (index) {
        case 'tab-home':
          // 店铺首页
          this.$util.gioTrackWithVariable('mall_store_click_bottomTab', {
            store_bottomTab_name: '店铺首页'
          })
          break;
        case 'tab-product':
          // 全部商品
          this.$util.gioTrackWithVariable('mall_store_click_bottomTab', {
            store_bottomTab_name: '全部商品'
          })
          break;
        case 'tab-category':
          // 商品分类
          this.$util.gioTrackWithVariable('mall_store_click_bottomTab', {
            store_bottomTab_name: '商品分类'
          })
          break;
      }
      if (this.bottomTabType !== index) {
        this.bottomTabType = index;
      }
    },
    openSearch() {
      this.$util.gioTrackWithVariable('mall_store_click_search', {
        storeId: this.shopStoreId ? this.shopStoreId + '' : 'null'
      })
      this.$router.push(`/goodsSearch?visitStore=${this.shopStoreId}&storeType=${this.storeSign.storeType}`);
    },
    openShareModal () {
      if (this.$util.isSgRn()) {
        this.doShare()
      } else {
        this.$util.gioTrackWithVariable('mall_store_click_share', {
          storeId: this.$route.query.storeId ? this.$route.query.storeId + '' : 'null'
        })
        this.showShareModal = true
      }
    },
    shareLinkFactory() {
      let url = `${location.origin}${location.pathname}`
      const query = this.getCleanQuerys()

      if (this.mId) {
        query.shareId = this.mId
      }
      if (this.storeSign.mdCode) {
        query.mdCode = this.storeSign.mdCode
      }

      const queryString = this.$util.parseParamsToQuery(query)
      return url + queryString
    },
    copyLink () {
      const url = this.shareLinkFactory()
      const a = document.createElement('a')
      const clipboard = new ClipboardJS(a, {
        text: function (trigger) {
          return url
        }
      });
      clipboard.on('success', () => {
        this.$toast('复制链接成功');
      })
      clipboard.on('error', () => {
        this.$toast('复制链接失败');
      })
      a.click()
    },
    doShare (params = {}) {
      const url = this.shareLinkFactory()
      this.$util.inAppShare({
        title: this.storeSign.o2oStoreName || this.shopBasicInfo.storeName,
        desc: this.$util.isSyBird() ? '定制智慧家 就找三翼鸟' : '海尔智家商城，海尔家电正品保障',
        url,
        ...params
      })
    },
    toogleModal(show) {
      this.showAddress = show;
    },
    bindScroll(ev) {
      let scrollTop = ev.target.scrollTop
      // console.log('scrollTop', scrollTop)
      if (scrollTop > 200) {
        this.showTotop = true
      } else {
        this.showTotop = false
      }
    },
    /**
     * 模拟动画滚动
    */
    scrollSmoothTo(position) {
      console.log('#app-warp scroll')
      if (!window.requestAnimationFrame) {
        window.requestAnimationFrame = function(callback) {
          return setTimeout(callback, 17);
        };
      }
      // debugger
      let _target = document.querySelector('#app-warp')
      // 当前滚动高度
      let scrollTop = _target.scrollTop;
      console.log(scrollTop, 'scrollTop')
      // 滚动step方法
      let step = function () {
        // 距离目标滚动距离
        var distance = position - scrollTop;
        // 目标滚动位置
        scrollTop = scrollTop + distance / 10;
        if (Math.abs(distance) < 1) {
          _target.scrollTo(0, position);
        } else {
          _target.scrollTo(0, scrollTop);
          requestAnimationFrame(step);
        }
      };
      step();
    },
    toGotop() {
      // document.getElementById('app-warp').scroll({
      //   top: 0,
      //   behavior: 'smooth',
      // })
      this.scrollSmoothTo(0)
    },
    changeLocation() {
      this.addrShow = true
    },
    onHideAddr() {
      this.addrShow = false
    },
    handleLocation() {
      this.locationshow = false
      this.addrShow = false
      if (this.$util.isSgRn()) {
        window.postMessage(JSON.stringify({
          type: 'setLocation',
          data: {
            location: this.location,
          }
        }));
      }
      this.isOutOfLoaction = false;
      this.getMemberStoreId(); // 地址切换后，需要重新调用所有以地址信息作为入参的接口，相当于刷新整个页面信息，直接重新调用getMemberStoreId。
    },
    otherLocationOption() {
      this.locationshow = true
    },
    onHideLocation() {
      this.locationshow = false
    },
    /**
     * @description  我的收获地址列表
     * 返回接口请求数据
    */
    getAddress() {
      const context = this;
      const data = {
        s: this.$route.query.storeId
      }
      personalApi.getAddressList(data).then(res => {
        const _resData = res.data;
        if (_resData.success) {
          console.log('addrData')
          context.addrData = _resData.data || [];
          console.log(context.addrData)
        }
      })
    },
    // 获取店铺地址
    getSubStore() {
      getSubStoreList({
        storeId: this.shopStoreId || this.$route.query.storeId,
        longitude: this.location.longitude,
        latitude: this.location.latitude
      }).then(res => {
        if (res.success) {
          this.subStoreList = res.data || [];
        } else {
          this.$toast(res.message || '获取店铺列表失败，请稍后重试');
        }
      })
    },
    openPictureShare () {
      this.showPictureShare = true
    }
  },
}
</script>
